@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Josefin Sans", sans-serif;
}

#header {
  background-color: #b9e5e8;
  padding: 25px 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: black;
  font-size: 35px;
  font-weight: 600;
}
.logo img {
  width: 40px;
  margin-left: 5px;
}

#navbar {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  margin-right: 50px;
}
#navbar ul {
  display: flex;
  list-style: none;
  gap: 50px;
}

#navbar ul li a {
  text-decoration: none;
  color: black;
  font-size: 20px;
  font-weight: 400;
}
.button-a {
  display: flex;
  gap: 10px;
}

.user-info {
  text-align: center;
  width: 100%;
}

.sign-up-btn,
.login-btn,
.logout-btn {
  position: relative;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 24px;
  background-color: white;
  border: 1px solid #4a628a;
  border-radius: 50px;
  text-decoration: none;
  color: #4a628a;
  font-size: 20px;
}

.sign-up-btn:hover,
.login-btn:hover,
.logout-btn:hover {
  transform: scale(0.98);
  transition: transform 0.2s ease-in-out;
  color: white;
  background-color: #4a628a;
}
.dropdown-menu {
  position: absolute;
  top: 40px;
  right: 20px;
  background: white;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 180px;
  padding: 10px;
  z-index: 99999;
}

.dropdown-menu p {
  font-size: 16px;
  margin: 10px 0;
  cursor: pointer;
  padding: 5px;
}

.dropdown-menu p:hover {
  background: #f1f1f1;
}
